.aboutPage {
    footer {
        padding: 200px 0 100px 0;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: var(--color7);

        p {
            max-width: 75ch;
        }

        a {
            color: var(--color4);
        }
    }
}

@media only screen and(max-width: 768px) {
    .aboutPage {
        padding: 1.5rem;

        footer {
            padding: 50px 0 50px 0;
        }
    }
}