
.block {
    position: relative;
    transition: opacity 200ms ease;

    h2,
    p {
        transition: all 200ms ease;
    }

    h2 {
        font-size: 2.5rem;
        color: var(--color5);
    }

    p {
        color: var(--color7);
        margin: 0;
        max-width: 85ch;
    }
}